@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300&display=swap');

html{
  height: fit-content;
}
body{
  overflow: hidden;
  margin: 0px;
  padding: 0;
  width: 100%;
  height: fit-content;
  background: #1E1E1E;
}
a{
  pointer-events: "none";
  user-select: "none";
}
.navbar{
  width: 220px;
  background: #171717;
  height: 100%;
  color: white;
}
.navbar-mobile{
  z-index: 100;
  position: absolute;
  width: 220px;
  background: #171717;
  height: 100%;
  color: white;
}

.icons{
  transition: 0.2s;
}
.icons:hover{
  cursor: pointer;
  transform: scale(1.2);
}

.App {
  padding: 0;
  margin: 0;
  display: flex;
  width: fit-content;
  height: 100vh;
  text-align: center;
  color: white;
  
  font-family: 'Inter', sans-serif;
  font-style: normal;
}
.scrollbar-hidden::-webkit-scrollbar {
  display: none;
}
.cast-collection{
  height: fit-content;
  width: 1000px;
  display: flex;
  overflow-x: auto;
  gap: 20px;
}
.top-movie-details{
  margin: 0;
  display: flex;
  width: 100%;
  justify-content: center;
}
.movie-detail{
  display: flex; 
  height: 90vh;
  padding-left: 200px; 
  padding-top: 100px; 
  padding-right: 1000px; 
  flex-direction: column; 
  overflow-y: scroll;
  overflow-x: hidden;
}
.trending-movies{
  padding: 0px;
  display: flex;
  justify-content: left;
  gap: 20px;
}
.movie-description{
  width: 500px;
  padding: 20px;
}
.movie-video-collection{
  margin-top: 50px;
  width: 700px;
}
.movie-grid{
  width: fit-content;
  gap: 1rem;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
}
.center{
  display: flex;
  justify-content: center;
  align-items: center;
}
.movie-poster{
  transition: all .15s ease-in-out;
}
.movie-poster:hover{
  transform: scale(1.05);
  filter: brightness(0.5);
  cursor: pointer;
  outline: 3px solid white;
}
.cast-profile{
  transition: all .05s ease-in-out;
}
.cast-profile:hover{
  filter: brightness(0.5);
}
.filter-button-collection{
  display: grid;
  gap: 20px;
  grid-template-columns: repeat(8, 1fr);
  width: fit-content;
}

.filter-buttons{
  width: 110px;
  height: 40px;
  border-radius: 40px;
  border: solid white 1px;
  font-weight: 600;
  background: transparent;
  color: white;
}
.filter-buttons:hover{
  cursor: pointer;
  background: white;
  color: black;
}
.filter-selected{
  width: 110px;
  height: 40px;
  border-radius: 40px;
  border: solid white 1px;
  font-weight: 600;
  background: white;
  color: black;
}
a{
  text-decoration: none;
  color:  white;
  transition: 0.1s;
}
a:hover{
  font-size: 17px;
}

.video-player{
  height: 100%;
  width: 100%;
  object-fit: cover;
  transition: 0.2 linear;
}

